import React from 'react'
import logo from './logo.svg';
import './App.css';

import app from './firebase'

const database = app.database()
class App extends React.Component {
  constructor() {
    super()
    this.state = {
      fooled: 0
    }
  }

  componentDidMount() {
    const main = async () => {
      await database.ref('/').on('value', (snapshot) => {
        this.setState({
          fooled: snapshot.val().fooled
        })
      })
      await database.ref('/').once('value', async (snapshot) => {
        await database.ref('/').update({
          fooled: snapshot.val().fooled + 1
        })
      })
    }
    main()
  }

  componentWillUnmount() {
    database.ref('/').off()
  }
  render() {
    return (
      <div class="background">
        <h1>April Fools!</h1>
        <h1>Fooled Users: {this.state.fooled}</h1>
        <h1>See you next year ;)</h1>
        <iframe
          class="videoplayer"
          width='95%'
          height="524"
          src="https://www.youtube.com/embed/iik25wqIuFo?autoplay=1"
          title="YouTube video player"
          frameborder="0"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
          allowfullscreen
        ></iframe>
      </div>
    );
  }
}

export default App;
